import './Card.css'

export const Card = props => {

    const { producto, agregarCarritoID } = props

    return (
        <div className="Card">
            <section>
                <h3>{producto.evento}</h3>
                <p><b className="precio">Precio: </b>${producto.precio}</p>
                <img src={producto.foto} alt="" />

                <p><b>Disponibilidad: </b>{producto.paquetesDisponibles}</p>
                <p><b>Entradas: </b>{producto.entradas}</p>
                <p><b>Hotel: </b>{producto.hotel}</p>
                <p><b>Categoría: </b>{producto.categoria}</p>
                <p><b>pensión Completa: </b>{producto.pensionCompleta? 'Si' : 'No'}</p>
                <p><b>Detalles: </b>{producto.detalles}</p>
                <br />
                <button onClick={
                    () => agregarCarritoID(producto.id)
                }>Agregar al carrito</button>
                <br />
            </section>
        </div>
    )
}