import { useEffect } from 'react';
import { NavLink } from "react-router-dom";

import './Index.css'

export function Index(props) {
    useEffect(() => {
        console.log('Componente Index Nosotros (montado)')
        
        return () => {
            console.log('Componente Index Nosotros (desmontado)')
        }
    },[])

    return (
        <div className="Nosotros">
            <h2 className="marca">SportAlive</h2>
            <p>Somos un agente turístico dedicado a generar experiencias durante grandes eventos deportivos</p>
            <p>Te proponemos propuestas incluyendo aereos, hotelería, seguro medico, traslados y entradas</p>
            <p>También podemos crearte una propuesta única para vos según tus necesidades</p>
            <p><span className="link"><NavLink to="/contacto">Contacto</NavLink></span> para saber mas y armar el viaje de tus sueños al evento deportivo que te guste</p>
        </div>
    )
}