import { useState } from 'react';

import './Index.css';

import Formulario from './Formulario';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { enviarDatosContacto } from '../Servicios/carrito';


export function Index(props) {

    const [formulario, setFormulario] = useState({
        nombre: '',
        apellido: '',
        mail: '',
        telefono: '',
        comentarios: ''
    });

    const [showAbm, setShowAbm] = useState(false);
    const [tituloAbmModal, setTituloAbmModal] = useState('');
    const [bodyAbmModal, setBodyAbmModal] = useState('');

    const handleCloseAbm = () => setShowAbm(false);
    const handleShowAbm = () => setShowAbm(true);

    function onChange(e) {
        const { id, value } = e.target;
        setFormulario({ ...formulario, [id]: value });
    }

    function borrarFormulario() {
        setFormulario({
            nombre: '',
            apellido: '',
            mail: '',
            telefono: '',
            comentarios: ''
        });
    }

    function formInvalid() {
        const noValido =
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(formulario.nombre) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(formulario.apellido) ||
            !/^\w+@\w+\.\w{2,3}(\.ar)?$/.test(formulario.mail) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,20}$/.test(formulario.telefono) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,500}$/.test(formulario.comentarios)

        return noValido;
    }

    async function onSubmit(e) {
        e.preventDefault()

        if(formInvalid()) {
            setTituloAbmModal('Validación de datos de contacto');
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,500}$/.test(formulario.comentarios)) {
                setBodyAbmModal('El campo Comentarios no puede ser nulo ni tener mas de 500 caracteres');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,20}$/.test(formulario.telefono)) {
                setBodyAbmModal('El campo Telefono no puede ser nulo ni tener mas de 20 caracteres');
            }
            if (!/^\w+@\w+\.\w{2,3}(\.ar)?$/.test(formulario.mail)) {
                setBodyAbmModal('El campo Mail no tiene un formato correcto');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(formulario.apellido) ) {
                setBodyAbmModal('El campo Apellido no puede ser nulo ni tener mas de 50 caracteres');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(formulario.nombre)) {
                setBodyAbmModal('El campo Nombre no puede ser nulo ni tener mas de 50 caracteres');
            }
        } else {
            setBodyAbmModal(`Se enviarán los datos de ${formulario.nombre} ${formulario.apellido}`);
            setTituloAbmModal('Envio de datos de contacto');
        }
        handleShowAbm();
    }

    async function abm(){
        const datosContacto = await enviarDatosContacto(formulario);
        borrarFormulario();
        handleCloseAbm();
    }

    return (
        <div className="Contacto">
                <Modal show={showAbm} onHide={handleCloseAbm} className='bgcModal'>
                    <Modal.Header closeButton>
                        <Modal.Title>{tituloAbmModal}</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>{bodyAbmModal}</Modal.Body>
                    
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseAbm}>{formInvalid()? 'Aceptar': 'Cancelar'}</Button>
                        {!formInvalid() && <Button variant="success" onClick={abm}>Enviar</Button>}
                    </Modal.Footer>
                </Modal>
                <Formulario
                    formulario={formulario}
                    onChange={onChange}
                    onSubmit={onSubmit}
                />
        </div>
    )
}