import './Formulario.css'

export default function Formulario(props) {

    const { nombre, apellido, mail, telefono,comentarios } = props.formulario;
    const { onChange, onSubmit } = props;

    return (
        <div className="Formulario">
            <form onSubmit={onSubmit} className="alta-form">
                <div className="input-group">
                    <label htmlFor="nombre">nombre</label>
                    <input type="text" id="nombre" value={nombre} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="apellido">apellido</label>
                    <input type="text" id="apellido" value={apellido} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="mail">mail</label>
                    <input type="mail" id="mail" value={mail} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="telefono">telefono</label>
                    <input type="text" id="telefono" value={telefono} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="comentarios">comentarios</label>
                    <input type="text" id="comentarios" value={comentarios} onChange={onChange} />
                </div>

                <button>Enviar Información</button>
            </form>
        </div>

    )
}