import './Tabla.css'


export const Tabla = props => {

    const { carrito, borrarID, incrementarCantID, decrementarCantID } = props

    return (
        <div className="TablaCarrito">

            <div className="table-responsive">
            <h3 className="text-center mb-4">Lista de productos seleccionados</h3>
                <table>
                    <thead>
                        <tr>
                            <th>evento</th>
                            <th>entradas</th>
                            <th>hotel</th>
                            <th>foto</th>
                            <th>precio</th>
                            <th>cantidad</th>
                            <th>subtotal</th>
                            <th>acciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            carrito.map((producto, index) =>
                                <tr key={index}>
                                    <td>{producto.evento}</td>
                                    <td>{producto.entradas}</td>
                                    <td>{producto.hotel}</td>
                                    <td><img width="100px" src={producto.foto} alt={"foto de " + producto.nombre} /></td>
                                    <td>$ {producto.precio}</td>
                                    <td>
                                        {producto.cantidad}
                                        <button className='btn btn-info ml-2 radius-custom' onClick={() => decrementarCantID(producto.id)}>-</button>
                                        <button className='btn btn-info ml-2 radius-custom' onClick={() => incrementarCantID(producto.id)}>+</button>
                                    </td>
                                    <td>${(producto.precio * producto.cantidad).toFixed(2)}</td>
                                    <td>
                                        <button className='btn btn-danger button-custom' onClick={() => borrarID(producto.id)}>Borrar</button>
                                    </td>
                                </tr>
                            )
                        }
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th><h5>TOTAL</h5></th>
                            <th>
                                <h4>
                                    ${ carrito.reduce((acc,p) => acc + Number(p.precio)*p.cantidad, 0).toFixed(2) }
                                </h4>
                            </th>
                            <th></th>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}
