import { useEffect, useState } from 'react'

import './Index.css'

import { obtenerProductos } from '../Servicios/productos';
import { Card } from './Card';
import { useLocalStorage } from '../Hooks/useLocalStore';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

export function Index(props) {
    const [productos, setProductos] = useState([]);
    const [carrito, setCarrito] = useLocalStorage('carrito', []);

    const [show, setShow] = useState(false);
    const [tituloModal, setTituloModal] = useState('');
    const [bodyModal, setBodyModal] = useState('');
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    useEffect(() => {
        console.log('Componente Index Inicio (montado)')

        async function pedir() {
            const productos = await obtenerProductos()
            setProductos(productos)
        }
        pedir()

        return () => {
            console.log('Componente Index Inicio (desmontado)')
        }
    }, [])


    function agregarCarritoID(id) {
        const producto = productos.find(p => p.id === id);
        setTituloModal('Producto Agregado al carrito');
        setBodyModal(`${producto?.evento} - ${producto?.entradas}`);

        const carritoClon = [...carrito];

        let pC = carritoClon.find(prodC => prodC.id === producto.id);
        if(!pC) {
            producto.cantidad = 1
            carritoClon.push(producto)
            setCarrito(carritoClon);
        }
        else {
            if (pC.cantidad < producto.paquetesDisponibles){
                pC.cantidad++;
                setCarrito(carritoClon);
            } else {
                setTituloModal('Producto sin disponibilidad en el carrito');
                setBodyModal(`${producto?.evento} - ${producto?.entradas} sin disponibilidad`);
            }

        }
        handleShow();
    }

    return (
        <div className="Inicio">
            <Modal show={show} onHide={handleClose} className='bgcModal'>
                <Modal.Header closeButton>
                    <Modal.Title>{tituloModal}</Modal.Title>
                </Modal.Header>
                <Modal.Body>{bodyModal}</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>Aceptar</Button>
                </Modal.Footer>
            </Modal>
            <div className="section-cards">
                <div className={`section-cards-header ${productos.length === 0? 'marginBottomSinCards' :''}`}>
                    <h2 className="marca">SportAlive</h2>
                    <h2>Próximos eventos - Paquetes</h2>
                </div>

                <div className="cards-container">
                    { productos.map( (producto, index) => 
                            <Card 
                                key={index} 
                                producto={producto} 
                                agregarCarritoID={agregarCarritoID}
                            />
                        )
                    }
                </div>

            </div>
        </div>
    )
}