import { useState } from 'react';

import './Index.css';

import { Tabla } from './Tabla';
import { useLocalStorage } from '../Hooks/useLocalStore';
import { enviarCarrito, getPreference } from '../Servicios/carrito'
import './pago.js'
import { Wallet } from '@mercadopago/sdk-react'

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

export function Index(props) {
    const [carrito, setCarrito] = useLocalStorage('carrito', []);
    const [compraStatus, setCompraStatus ] = useState({payment_id: 'null', status: 'null', merchant_order_id: 'null'})
    const [showBorrar, setShowBorrar] = useState(false);
    const [tituloBorrarModal, setTituloBorrarModal] = useState('');
    const [bodyBorrarModal, setBodyBorrarModal] = useState('');

    const handleCloseBorrar = () => setShowBorrar(false);
    const handleShowBorrar = () => setShowBorrar(true);

    const [borrarID, setBorrarID] = useState(null);
   
    function borrarAll() {
        setCarrito([]);
        handleCloseBorrar();
    }

    function borrarAllAbrirModal(){
        borrarAbrirModal(null);
    }

    function borrarAbrirModal(id){
        setBorrarID(id);
        if (id){
            const producto = carrito.find(p => p.id === id);
            setTituloBorrarModal('Eliminación en el carrito');
            setBodyBorrarModal(`¿Confirma la eliminación del ${producto.evento} - ${producto.entradas} del carrito?`);
        } else {
            setTituloBorrarModal('Eliminación del carrito');
            setBodyBorrarModal(`¿Confirma la eliminación del carrito?`);
        }
        handleShowBorrar();
    }

    function borrarIDIndividual() {
        const carritoClon = [...carrito]
        const index = carritoClon.findIndex(p => p.id === borrarID)
        carritoClon.splice(index,1)
        setCarrito(carritoClon);
        setBorrarID(null);
        handleCloseBorrar();
    }
    
    function incrementarCantID(id) {
        const carritoClon = [...carrito]
        const producto = carritoClon.find(p => p.id === id)
        if(producto.cantidad < producto.paquetesDisponibles) {
            producto.cantidad++
            setCarrito(carritoClon)
        }
    }
    
    function decrementarCantID(id) {
        const carritoClon = [...carrito]
        const producto = carritoClon.find(p => p.id === id)
        if(producto.cantidad > 1) {
            producto.cantidad--
            setCarrito(carritoClon)
        }
    }

    async function pedir(compraParam) {
        const carritoEnviado = await enviarCarrito(carrito, compraParam);
        setCarrito([]);
    }

    const customization = {
        texts: {
            action: 'buy',
            valueProp: 'security_details'
        },
        visual: {
            buttonBackground: 'black',
            borderRadius: '10px',
        }        
    }

    const onReady = async () => {
        console.log('onReady')

        const queryParameters = new URLSearchParams(window.location.search)
        const compraParam = {}
        compraParam.payment_id = queryParameters.get('payment_id') || 'null'
        compraParam.status = queryParameters.get('status') || 'null'
        compraParam.merchant_order_id = queryParameters.get('merchant_order_id') || 'null'
        console.log(compraParam)

        if(compraParam.payment_id !== 'null' && compraParam.status !== 'null' && compraParam.merchant_order_id !== 'null') {
            if(compraParam.status !== compraStatus.status) {
                setCompraStatus(compraParam)

                if(compraParam.status === 'approved') {
                    await pedir(compraParam)
                    window.location.href = '/';
                }
            }
        }
    }

    const onError = () => {
        console.log('onError')
    }

    const onSubmit = () => {
        console.log('onSubmit')

        return new Promise((resolve, reject) => {
            getPreference(carrito)
                .then(({data:response}) => {
                    console.log(response)
                    resolve(response.preferenceId)
                })                
                .catch(error => {
                    reject(error)
                })
        })
    }

    return (
        <div className="Carrito">

            {/* modal para borrar */}
            <Modal show={showBorrar} onHide={handleCloseBorrar} className='bgcModal'>
                <Modal.Header closeButton>
                    <Modal.Title>{tituloBorrarModal}</Modal.Title>
                </Modal.Header>

                <Modal.Body>{bodyBorrarModal}</Modal.Body>
                
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseBorrar}>Cancelar</Button>
                    <Button variant="success" onClick={borrarID? borrarIDIndividual: borrarAll}>Borrar</Button>
                </Modal.Footer>
            </Modal>

            {carrito.length === 0 && <h3 className='marginBottomSinCarrito text-center'>No se encontraron pedidos pre armados</h3>}
            {carrito.length > 0 &&
                <>
                    <Tabla 
                        carrito={carrito} 
                        borrarID={borrarAbrirModal}
                        incrementarCantID={incrementarCantID}
                        decrementarCantID={decrementarCantID}
                    />
                    <div className='row mt-4 div-botones'>
                        <div className="col-12 text-center"><button className="button-general carrito__borrar" onClick={borrarAllAbrirModal}>Borrar Carrito</button></div>
                    </div>
                    <div id="wallet_container">
                    <Wallet 
                        customization={customization}
                        onReady={onReady}
                        onError={onError}
                        onSubmit={onSubmit}
                    />
                </div>
                </>
            }
        </div>
    )
}