import { useEffect, useState } from 'react'

import './Index.css'

import Ingreso from './Ingreso'
import { Tabla } from './Tabla'

import { actualizarProducto, borrarProducto, guardarProducto, obtenerProductos } from '../Servicios/productos'

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';



export function Index(props) {

    const [productos, setProductos] = useState([])
    const [producto, setProducto] = useState({
        evento: '',
        precio: '',
        paquetesDisponibles: '',
        entradas: '',
        hotel: '',
        categoria: '',
        detalles: '',
        foto: '',
        pensionCompleta: false
    })
    const [editarID, setEditarID] = useState(null)

    // ------------------------------------------------------------------
    //   Control del menú modal de confirmación de borrado de producto
    // ------------------------------------------------------------------
    const [showBorrar, setShowBorrar] = useState(false);
    const [borrarID, setBorrarID] = useState(null)

    const handleCloseBorrar = () => setShowBorrar(false);
    const handleShowBorrar = () => setShowBorrar(true);

    // ------------------------------------------------------------------
    //   Control del menú modal de abm de producto
    // ------------------------------------------------------------------
    const [showAbm, setShowAbm] = useState(false);
    const [tituloAbmModal, setTituloAbmModal] = useState('');
    const [bodyAbmModal, setBodyAbmModal] = useState('');

    const handleCloseAbm = () => setShowAbm(false);
    const handleShowAbm = () => setShowAbm(true);




    useEffect(() => {
        console.log('Componente Index Alta (montado)')

        async function pedir() {
            const productos = await obtenerProductos()
            setProductos(productos)
        }
        pedir()

        return () => {
            console.log('Componente Index Alta (desmontado)')
        }
    }, [])


    // ----------------------------------------------
    //   Actualización de cada campo del formulario
    // ----------------------------------------------
    function onChange(e) {
        const { type, id, value, checked } = e.target
        //console.log(type, value, checked, id)
        setProducto({ ...producto, [id]: type === 'checkbox' ? checked : value })
    }

    // ----------------------------------------------
    // limpieza del formulario de entrada de producto
    // ----------------------------------------------
    function borrarFormulario() {
        setProducto({
            evento: '',
            precio: '',
            paquetesDisponibles: '',
            entradas: '',
            hotel: '',
            categoria: '',
            detalles: '',
            foto: '',
            pensionCompleta: false
        })
    }

    function formInvalid() {

        const noValido =
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(producto.evento) ||
            !producto.precio || producto.precio <= 0 ||
            !producto.paquetesDisponibles || producto.paquetesDisponibles <= 0 || !Number.isInteger(+producto.paquetesDisponibles) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,40}$/.test(producto.hotel) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,40}$/.test(producto.entradas) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ*]{1,40}$/.test(producto.categoria) ||
            !/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,100}$/.test(producto.detalles) ||
            !producto.foto
        return noValido
    }

    // --------------------------------------------------
    //    Incorporación / actualización de un producto
    // --------------------------------------------------
    function onSubmit(e) {
        e.preventDefault();
        if(formInvalid()) {
            setTituloAbmModal('Validación de producto');
            if (!producto.foto) {
                setBodyAbmModal('El campo Foto no puede ser nulo');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,100}$/.test(producto.detalles) || producto.detalles.length > 100) {
                setBodyAbmModal('El campo Detalles no puede ser nulo ni tener mas de 100 caracteres');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ*]{1,40}$/.test(producto.categoria) || producto.categoria.length > 40) {
                setBodyAbmModal('El campo Categoria no puede ser nulo ni tener mas de 40 caracteres');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,40}$/.test(producto.hotel) || producto.hotel.length > 40 ) {
                setBodyAbmModal('El campo Hotel no puede ser nulo ni tener mas de 40 caracteres');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,40}$/.test(producto.entradas) || producto.entradas.length > 40) {
                setBodyAbmModal('El campo Entradas no puede ser nulo ni tener mas de 40 caracteres');
            }
            if (!producto.paquetesDisponibles || producto.paquetesDisponibles <= 0 || !Number.isInteger(+producto.paquetesDisponibles)) {
                setBodyAbmModal('El campo Productos Disponibles no puede ser nulo ni menor a 0 ni permite decimales');
            }
            if (!producto.precio || producto.precio <= 0) {
                setBodyAbmModal('El campo Precio no puede ser nulo ni menor a 0');
            }
            if (!/[a-zA-Z0-9ñÑáéíóúÁÉÍÓÚ]{1,50}$/.test(producto.evento) || producto.evento.length > 50) {
                setBodyAbmModal('El campo Evento no puede ser nulo ni tener mas de 50 caracteres');
            }
        } else {
            if (editarID) {
                setBodyAbmModal(`Se modificará el evento:${producto.evento} - ${producto.entradas}`);
                setTituloAbmModal('Modificación de producto');
            } else {
                setBodyAbmModal(`Se agregará el evento:${producto.evento} - ${producto.entradas}`);
                setTituloAbmModal('Alta de producto');
            }
        }
        handleShowAbm();
    }

    async function abm() {
        const productosClon = [...productos]

        if (!editarID) {
            const productoGuardado = await guardarProducto(producto)
            productosClon.push(productoGuardado)
        }
        else {
            const id = editarID
            const productoActualizado = await actualizarProducto(id, producto)
            const index = productosClon.findIndex(p => p.id === productoActualizado.id)
            productosClon.splice(index, 1, producto)
            setEditarID(null)
        }
        setProductos(productosClon);
        borrarFormulario();
        handleCloseAbm();
    }

    // ----------------------------------------------
    //           Edición de un producto
    // ----------------------------------------------
    function editar(id) {
        console.log('editar', id)

        if (!editarID || editarID !== id) {
            setEditarID(id)
            setProducto(productos.find(p => p.id === id))
        }
        else {
            setEditarID(null)
            borrarFormulario()
        }
    }

    // ----------------------------------------------
    //         Eliminación de un producto
    // ----------------------------------------------
    function borrar(id) {
        console.log('borrar', id)

        if(id) {
            setBorrarID(id)
            handleShowBorrar()
        }
    }

    async function goBorrar() {
        const id = borrarID

        if(id) {
            const productoEliminado = await borrarProducto(id)
            const productosClon = [...productos]
            const index = productosClon.findIndex(p => p.id === productoEliminado.id)
            productosClon.splice(index, 1)
            setProductos(productosClon)
        }
        handleCloseBorrar()
    }

    function enviarUrlImagen(url) {
        console.log('urlImagen:', url)

        const productoClon = { ...producto }
        productoClon.foto = url
        setProducto(productoClon)
    }

    return (
        <div className="Alta">
                {/* modal de borrado */}
                <Modal show={showBorrar} onHide={handleCloseBorrar} className='bgcModal'>
                    <Modal.Header closeButton>
                        <Modal.Title>Borrado de Producto</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        ¿Desea eliminar el evento "{productos.find(p => p.id === borrarID)?.evento}"?
                    </Modal.Body>
                    
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseBorrar}>Cancelar</Button>
                        <Button variant="danger" onClick={goBorrar}>Aceptar</Button>
                    </Modal.Footer>
                </Modal>

                {/* modal para crear / modificar / validaciones */}
                <Modal show={showAbm} onHide={handleCloseAbm} className='bgcModal'>
                    <Modal.Header closeButton>
                        <Modal.Title>{tituloAbmModal}</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>{bodyAbmModal}</Modal.Body>
                    
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseAbm}>{formInvalid()? 'Aceptar': 'Cancelar'}</Button>
                        {!formInvalid() && <Button variant="success" onClick={abm}>{editarID? 'Modificar': 'Agregar'}</Button>}
                    </Modal.Footer>
                </Modal>

                <Ingreso
                    producto={producto}
                    onChange={onChange}
                    onSubmit={onSubmit}
                    editarID={editarID}
                    enviarUrlImagen={enviarUrlImagen}
                />

                <Tabla
                    productos={productos}
                    editar={editar}
                    borrar={borrar}
                    editarID={editarID}
                />
        </div>
    )
}