import axios from "axios"

const URL_API_CARRITO = process.env.NODE_ENV === 'production'
                                    ? '/api/carrito/'
                                    : `http://localhost:${process.env.REACT_APP_PORT_SRV_DEV}/api/carrito/`

//se envia el carrito y ademas datos de contacto para el pedido
export async function enviarCarrito(carrito, compraParam) {
    const objetoCarrito = {
        pedido: carrito,
        compra: compraParam
    }
    try {
        const { data: carritoGuardado } = await axios.post(URL_API_CARRITO, objetoCarrito);
        return carritoGuardado;
    }
    catch(error) {
        console.error('ERROR POST AXIOS:', error.message);
        return {}
    }
}

export async function enviarDatosContacto(formulario) {
    const objetoContacto = {
                                pedido: null,
                                compra: null,
                                nombre: formulario.nombre,
                                apellido: formulario.apellido,
                                mail: formulario.mail,
                                telefono: formulario.telefono,
                                comentarios: formulario.comentarios
                            }
    try {
        const { data: carritoGuardado } = await axios.post(URL_API_CARRITO, objetoContacto);
        return carritoGuardado;
    }
    catch(error) {
        console.error('ERROR POST AXIOS:', error.message);
        return {}
    }
}

export async function getPreference(carrito) {
    const requestBody = {
        items: carrito.map(p => ({
            id: p._id,
            title: p.entradas,
            description: p.evento +' - '+ p.entradas + ' - Hotel: '+ p.hotel,
            quantity: parseInt(p.cantidad),
            unit_price: Number(p.precio)
        }))
    }

    console.log(requestBody)

    return axios.post(URL_API_CARRITO + 'mp/create_preference', requestBody)
}


