import './Ingreso.css'
import { ObtenerFoto } from './ObtenerFoto'

export default function Ingreso(props) {

    const { evento, precio, paquetesDisponibles, entradas, hotel, categoria, detalles, foto, pensionCompleta } = props.producto
    const { onChange, onSubmit, editarID, invalid, enviarUrlImagen } = props

    return (
        <div className="Ingreso">
            <form onSubmit={onSubmit} className="alta-form">
                <div className="input-group">
                    <label htmlFor="evento">evento</label>
                    <input type="text" id="evento" value={evento} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="precio">precio</label>
                    <input type="number" id="precio" value={precio} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="paquetesDisponibles">paquetes Disponibles</label>
                    <input type="number" id="paquetesDisponibles" value={paquetesDisponibles} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="entradas">entradas</label>
                    <input type="text" id="entradas" value={entradas} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="hotel">hotel</label>
                    <input type="text" id="hotel" value={hotel} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="categoria">categoria</label>
                    <input type="text" id="categoria" value={categoria} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="detalles">detalles</label>
                    <input type="text" id="detalles" value={detalles} onChange={onChange} />
                </div>

                <div className="input-group">
                    <label htmlFor="foto">foto</label>
                    <input type="text" id="foto" value={foto} onChange={onChange} />
                </div>

                {/* Zona de obtención de la foto del producto */}
                <ObtenerFoto enviarUrlImagen={enviarUrlImagen}/>

                <div className="input-group form-check">
                    <input type="checkbox" id="pensionCompleta" className="mr-2" checked={pensionCompleta} onChange={onChange} />
                    <label htmlFor="pensionCompleta">pension Completa</label>
                </div>

                <button disabled={invalid}>
                    { editarID? 'Modificar' : 'Agregar' }
                </button>
            </form>
        </div>

    )
}