import './Tabla.css'


export const Tabla = props => {

    const { productos, editar, borrar, editarID } = props
    //console.log(productos)

    return (
        <div className="TablaAlta">

            { productos.length === 0 && <h3 className='sinProductos'>No se encontraron productos</h3> }

            { productos.length > 0 &&
                <div className="table-responsive">
                <h2>Lista de productos disponibles</h2>
                    <table>
                        <thead>
                            <tr>
                                <th>evento</th>
                                <th>precio</th>
                                <th>paquetes Disponibles</th>
                                <th>entradas</th>
                                <th>hotel</th>
                                <th>categoría</th>
                                <th>detalles</th>
                                <th>foto</th>
                                <th>pension Completa</th>
                                <th>acciones</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                productos.map( (producto,index) => 
                                    <tr key={index}>
                                        <td>{producto.evento}</td>
                                        <td>${producto.precio}</td>
                                        <td>{producto.paquetesDisponibles}</td>
                                        <td>{producto.entradas}</td>
                                        <td>{producto.hotel}</td>
                                        <td>{producto.categoria}</td>
                                        <td>{producto.detalles}</td>                           
                                        <td><img width="100%" src={producto.foto} alt={'foto de ' + producto.evento} /></td>
                                        <td>{producto.pensionCompleta? 'Si':'No'}</td>                           
                                        <td>
                                            <button className={`btn btn-${editarID && editarID === producto.id?'outline-':''}warning mb-2 button-custom`} onClick={
                                                () => editar(producto.id)
                                            }>{ editarID && editarID === producto.id? 'Cancelar' : 'Editar' }</button>
                                            <br />
                                            <button disabled={editarID? true : false} className="btn btn-danger button-custom" onClick={
                                                () => borrar(producto.id)
                                            }>Borrar</button>
                                        </td>                           
                                    </tr> 
                                )
                            }
                        </tbody>
                    </table>
                </div>                
            }
        </div>
    )
}
