import './Navbar.css';

import { NavLink } from "react-router-dom";


export const Navbar = () =>
    <nav className="navbar navbar-expand-md navbar-dark background">
        <NavLink className="logo" to="/"></NavLink>
        
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse justify-center" id="navbarNav">
            <ul className="navbar-nav">
            <li className="nav-item">
                    <NavLink className="nav-link nav-link-custom" to="/inicio">Inicio</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link-custom" to="/alta">Alta</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link-custom" to="/contacto">Contacto</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link-custom" to="/nosotros">Nosotros</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link-custom" to="/carrito">Carrito</NavLink>
                </li>
            </ul>
        </div>
        <div className="pelota"></div>
    </nav>
